body {
    background: url(/react-app/static/media/full-bloom.c614347a.png);
}

div {
    text-align: center;
}

.add-project {
    color: #333;
    font-size: 36px;
    font-family: 'Merriweather Sans', sans-serif;
    padding: 25px 25px 0 25px;
}

.labels {
    display: inline-block;
    font-size: 25px;
    font-weight: bold;
    font-family: 'Merriweather Sans', sans-serif;
    margin: 0 0 20px 0;
    padding: 10px;
    text-decoration: underline;
}

input {
    border: none;
    padding: 14px;
    font-size: 16px;
    margin-bottom: 20px;
}

input:focus {
    outline: 0;
}

select {
    padding: 10px;
    font-size: 16px;
    margin-top: 10px;
}

.submit{
    background: #006666;
    color: #fff;
    text-transform: uppercase;
    border-radius: 5px;
    -webkit-appearance: none;
}

.latest-projects {
    text-decoration: underline;
    font-size: 25px;
    font-family: 'Merriweather Sans', sans-serif;
}

li {
    display: flex;
    justify-content: center;
    text-align: left;
}

button {
    font-size: 22px;
    text-decoration: none;
    margin-left: 15px;
    margin-top: -3px;
    padding: 0px 10px;
    background: #006666;
    color: #fff;
    border-radius: 5px;
    border: none;
}

hr {
    width: 100px;
    border: 1px solid #333;
    margin: 30px auto;
}

Todos {
    margin-bottom: 20px;
}

@media screen and (max-width: 700px) {
    .add-project {
        font-size: 34px;
        font-family: 'Merriweather Sans', sans-serif;
        padding: 15px 25px 0 25px;
    }

    .labels {
        display: inline-block;
        font-size: 25px;
        font-weight: bold;
        font-family: 'Merriweather Sans', sans-serif;
        margin-bottom: 10px;
        padding: 0;
        text-decoration: underline;
    }

    select {
        margin-top: 0px;
    }
}

